#view_data_modal{
    width: 600px;
    min-height: 200px;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 50px;
    padding-bottom: 10px;
    border: 1px solid #ccc;
    border-radius: 2px;
}

@media only screen and (max-width: 600px) {
    #view_data_modal{
        width: 100vw;
    }

    .edit_json{
        display: none;
    }
}

#view_data_header{
    position: relative;
    width: 100%;
    height: 50px;
    background: #474787;
    margin: 0;
}

#view_data_inheader{
    width: 90%;
    height: 100%;
    display: flex;
    align-items: center;
    margin: 0 auto;
   
}

#view_data_title{
    color: white;
}

.edit_json{
    position: absolute;
    top: 0px;
    bottom:0px;
    margin-top: auto;
    margin-bottom: auto;
    right: 10px;
    height: 18px;
    color:white;
}

.edit_json:hover{
    cursor: pointer;
    color: #6ba2ff;
    text-decoration: underline;
}

#view_data_main{
    width: 90%;
    display: block;
    margin: 0 auto;
    padding: 1px;
}

.view_data_value{
    font-size: 18px;
    margin: 0;
}

/* these are for pretty print */
pre{
    word-wrap:break-word;
    white-space: pre-line;
    margin: 0;
}



#view_data_btns{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}

#save_doc{
    box-shadow: inset 0px 0px 0px 1px #ccc;
    background: #eee;

}

#save_doc:hover{
    cursor: pointer;
    box-shadow: inset 0px 0px 0px 1px #aaa;
}

#delete_doc{
    color: #f5613d;
    background: #eee;
    box-shadow: inset 0px 0px 0px 1px #ccc;
}

#delete_doc:hover{
    cursor: pointer;
    background: #e84118;
    color: white;
}