
.data{
    width: 48%;
    max-width: 400px;
    border-radius: 3px;
    background: white;
    box-shadow: inset 0 0 0 1px #ccc;
    overflow-wrap: break-word;
}

.text{
   min-height: 100px;
}

.data_image{
    width: 50px;
    height: 50px;
    display: flex;
    margin: 0 auto;
    margin-top: 5px;
    margin-bottom: 5px;
    justify-content: center;
    align-items:center;
    border: 1px solid #ccc;
    color: #2c2c54;
    border-radius: 3px;
}

.data_image:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

.data_type{
    text-align: center;
    margin-top: 0;
}

.modal_image{
    width: 100%;
    display: block;
    margin: 0 auto;
}

.data_key{
    background: #2c2c54;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    display: flex;
    align-items: center;
    padding: 1px;
}

.data_key:hover{
    cursor: pointer;
}

.data_key:hover h1{
    color: #6ba2ff;
    text-decoration: underline;
}

.data_key h1{
    font-weight: 500;
    font-size: 16px;
    color: white;
    padding-left: 5px;
}

.data_body{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap:5px;
    padding: 5px;
}

.data_value_container{
    border: 1px solid #ccc;
    border-radius: 3px;
    display: flex;
    gap: 5px;
    align-items: center;
    padding-right: 1px;
    align-items: stretch; /* this is great */
}

.data_value_container p{
    margin: 0;
}

.important_data{
    background: #e1e1f7;
}

.data_value_left{
    background: #ccc;
    padding-right: 2px;
    padding-left: 2px;
    color: #2c2c54;
    font-weight: 500;
    display: flex;
    align-items: center;
  
}

.data_value{
    padding:2px;
    padding-left: 0px;
    margin: 0;
}

.filetext{
    white-space: pre-line;
}