

*:focus{
    outline: none;
    
}
body{
    background: #f0f0f0;
}

a{
    color:#474787;
}

button{
    z-index: 1;
    position: relative;
    background:white;
    border:none;
    height: 30px;
    font-size: 16px;
    border-radius: 3px;
    padding: 0px 8px;
    display: flex;
    align-items: center;
}

button:hover{
    cursor: pointer;
    background:#e1e1f7;
}

button p{
    margin-left: 10px;
}

select:hover{
    cursor: pointer;
}

.folder{
    background: white;
    min-width: 100px;
    max-width: 350px;
    padding: 20px;
    margin-top: 10px;
    border: 1px solid #ccc;
    border-radius: 3px;
    overflow-wrap: break-word;
    
}

.folder:hover{
    cursor: pointer;
    border: 1px solid #aaa;
}

.folder:hover .folder_name{
   text-decoration: underline;
}

.folder_name{
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 24px;
    height: 60px;
    margin-top: 10px;
}

.folder_image{
    width: 50px;
    height: 50px;
    margin: 0 auto;
    background: #3d3d75;
    border-radius: 3px;
}

.folder_docs{
    padding: 0;
    margin: 0;
    text-align: center;
    font-size:16px;
}

.folder_docs span{
    font-weight: 500;
}

/* split this */
.second-modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 101;
}

.modal-position{
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    z-index: 10;
    overflow-y: auto;
}
.modal_title{
    color: black;
}
.modal_container{
    padding: 10px;
    width: 90%;
    display: block;
    margin: 0 auto;
}
