#add_data_modal{
    width: 100%;
    max-width: 400px;
    height: 420px;
    border-radius: 2px;
    border: 1px solid #aaa;
    background: white;
    display:block;
    margin: 0 auto;
    margin-top: 30px;
    
}
#add_data_modal_container{
    width: 90%;
    display: block;
    margin: 0 auto;
}

/* make a class */
#add_data_folder:nth-child(2){
    margin-top: -10px;
}

.add_data_info{
    margin: 0;
    margin-top: 5px;
    font-size: 12px;
    color: #aaa;
}

#add_data_folder{
    width: 314px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}

#add_data_key{
    width: 300px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
}

#add_data_key::placeholder{
    color: black;
}

#add_data_value{
    width: 300px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding-left: 10px;
    padding-top: 5px;
    resize: none;
}

#add_data_file{
    display: none;
}

#attach_file{
    display: block;
    width: 80px;
    border-radius: 3px;
}

#attach_file p{
    margin: 0;
    width: 80px;
    text-decoration: underline;
    color: #3d3d75;
}


#attach_file:hover{
    cursor: pointer;
    background: #e1e1f7;
}

#add_data_btn{
    box-shadow: inset 0px 0px 0px 1px #ccc;
    background: #eee;
    margin-top: 30px;
}
#add_data_btn:hover{
    box-shadow: inset 0px 0px 0px 1px #aaa;
    cursor: pointer;
    background:#e1e1f7;
}

.spinning_big{
    position: relative;
    height:100px;
    width: 100px;
    border-top-right-radius: 1px;
    border-bottom-left-radius: 1px;
    animation-name: spinning;
    animation-duration: 2s;
    animation-iteration-count: infinite
}

.spinning{
    border-radius: 3px;
    animation-name: spinning;
    animation-duration: 2s;
    animation-iteration-count: infinite
}

@keyframes spinning {
    0% {transform: rotate(0deg)}
    100% {transform: rotate(360deg)}
  }