#update_data{
    position:fixed;
    width: 100%;
    height: 70px;
    top: 50px;
    background: #f0f0f0;
    border-bottom: 1px solid #ccc;
    display:flex;
    align-items: center;
    z-index: 2;
}

@media only screen and (max-width: 400px) {

    #update_data_container{
        width: 90%;
        display:flex;
        margin: 0 auto;
        align-items: center;
        justify-content: space-between;
    }
    #replicate{
        display: none;
    }
    .side_btn{
        display: none;
    }
    #update_data_container_left{
        display: flex;
        align-items: center;
        gap: 15px;
    }
}

@media only screen and (min-width:400px){
    #update_data_container{
        min-width: 500px;
        width: 90%;
        display:flex;
        margin: 0 auto;
        align-items: center;
        justify-content: space-between;
    }
    #update_data_container_left{
        display: flex;
        align-items: center;
        gap: 15px;
        min-width: 300px;
    }
    .side_btn{
        display: flex;
        align-items: center;
        gap:10px;
    }
}




#update_data_container_left h1{
    font-size: 20px;
}

#user{
    display: flex;
    align-items: center;
    background: #eee;
    height: 25px;
    padding: 0px 8px;
    gap: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

#user:hover{
    cursor: pointer;
    border: 1px solid #aaa
}

.btn_accent{
    position: absolute;
    z-index: 1;
    left:0px;
    width: 10px;
    height: 100%;
    background:#ffa801;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

#add_data:hover{
    cursor: pointer;
    background:#e1e1f7;
}

